<template lang="html">
    <main-layout :settings-breadcrumbs="settingsBreadcrumbs">
        <template #content>
            <profile-form-layout
                :show-modal="showModal"
                @validate="validate"
                @close-modal="showModal=false"
                @confirm="confirm"
            >
                <template #headerIcon>
                    <account-multiple />
                </template>
                <template #form>
                    <div class="profile-info__form">
                        <div class=" order-form-step__column order_form">
                            <form-select-writers
                                v-model="preferred_writers"
                                name="preferred_writers"
                                label="Preferred writers"
                                :writers="listWriters"
                                :styles="true"
                                :disabled_items="blocked_writers"
                            />
                        </div>
                        <div class=" order-form-step__column order_form">
                            <form-select-writers
                                v-model="blocked_writers"
                                name="blocked_writers"
                                label="Blocked writers"
                                :writers="listWriters"
                                :styles="false"
                                :disabled_items="preferred_writers"
                            />
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'

import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'

import {
    mapClientFields
} from '@/store/modules/client';
import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';
import AccountMultiple from 'mdi-vue/AccountMultiple'

import { createNamespacedHelpers } from 'vuex'
import {
    GET_LIST_WRITERS
} from '@/store/modules/config/action-types'

import {
    ACTION_CLIENT_UPDATE_WRITERS
} from '@/store/modules/client/action-types';

const { mapActions: mapConfigActions } = createNamespacedHelpers('config')

const {
    mapActions: mapClientActions
} = createNamespacedHelpers('client');

export default {
    name: 'ProfileWriters',
    components: {
        MainLayout,
        ProfileFormLayout,
        AccountMultiple
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
    },
    data() {
        return {
            listWriters: null,
            showModal: false,
            settingsBreadcrumbs: {
                title: 'Writer Preferences',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: '/cp/profile/writers', title: 'Writer Preferences' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            },
            loading: false
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.preferred_writers',
            'client_data.blocked_writers'
        ])
    },
    async mounted() {
        this.listWriters = await this[GET_LIST_WRITERS]();
        eventBus.$on('saveSettings', () => {
            this.$v.$reset()
            this.validate()
        })
    },
    beforeDestroy() {
        eventBus.$off('saveSettings')
    },
    methods: {
        ...mapConfigActions([
            GET_LIST_WRITERS
        ]),
        ...mapClientActions({
            updateWriters: ACTION_CLIENT_UPDATE_WRITERS
        }),
        async confirm() {
            try {
                this.loading = true
                await this.updateWriters({
                    preferred_writers: this.preferred_writers.map((a) => +a.sw_id),
                    blocked_writers: this.blocked_writers.map((a) => +a.sw_id)
                })
                eventBus.$emit('showSnackBar', 'Your writers list has been changed', 'success');
                this.$router.push({ name: 'profile' })
            } catch (e) {
                eventBus.$emit('showSnackBar', e.response.data.message, 'error');
            } finally {
                this.loading = false
            }
        },
        async submit() {
            this.showModal = true
        }
    }
}
</script>
